import React from "react"
import { Link, graphql, StaticQuery } from "gatsby"
import './tags.css'

export default () => (
  <StaticQuery
    query={graphql`
    query {
      allMarkdownRemark(
        sort: { order: DESC, fields: [frontmatter___tags] }
        limit: 2000
        ) {
        group(field: frontmatter___tags) {
          fieldValue
          totalCount
        }
      }
    }
  `}
    render={data => (
        data.allMarkdownRemark.group.map(item =>
          <Link to={`/tags/${ item.fieldValue.replace(" ", "-") }`} className="tags" activeStyle={{ background: 'rgba(252, 214, 112, 1)', boxShadow: '0 0 10px rgba(245, 171, 53, .3)', textShadow: '0 0 2px rgba(0, 0, 0, .5)', color: '#ffffff' }}><span>#{ item.fieldValue.replace("-"," ") }</span></Link>
        )
    )}
  />
)